.form-inside {
  padding: var(--pad);
  padding-bottom: 30px;
  letter-spacing: -0.3px;
  /* display: flex;
  flex-direction: column; */
}

form {
  width: 500px;
  flex-grow: 1;
  position: relative;
}

.form {
  flex-grow: 1;
}

h1 {
  color: var(--Marine-blue);
  font-weight: 900;
  margin-bottom: 10px;
}

h1+p {
  margin-bottom: 32px;
}

.inform {
  color: var(--Marine-blue);
  margin-bottom: 20px;
}

.inform p {
  margin: 0;
}

.label-group {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 5px;
}

label {
  /* display: block; */
  padding: 6px 0;
}

input {
  width: 100%;
  padding: 16px;
  border-radius: calc(var(--rad)/2);
  border: 1px solid var(--Cool-gray);
  color: var(--Marine-blue);
  font-weight: 700;
  font-size: 18px;
}

input:focus {
  outline: none;
  border-color: var(--Marine-blue);
}

input:focus::placeholder {
  opacity: 0;
}

input::placeholder {
  font-weight: 500;
  color: var(--Cool-gray);
  transition: .3s;
  font-size: 18px;
}

input.error {
  border-color: var(--Strawberry-red);
}

p.error-msg {
  color: var(--Strawberry-red);
  font-weight: 700;
}

button:disabled {
  opacity: .3;
}

.plan .JoySheet-root {
  width: calc(100%/3 - 15px) !important;
  padding: 20px !important;
  padding-bottom: 10px !important;
}

.plan .JoyRadio-action {
  transition: .2s !important;
  border-color: var(--Cool-gray) !important
}

.plan .Joy-checked,
.plan .JoyRadio-action:hover {
  border-color: var(--Purplish-blue) !important;
  background-color: rgba(173, 189, 255, 0.2) !important;
  z-index: 0;
}

.plan p {
  color: var(--Cool-gray) !important;

}

.plan .typo {
  z-index: 0;
}

h2 {
  font-size: 1.1rem !important;
  color: var(--Marine-blue) !important;
  font-weight: 600;
}

.JoyBox-root {
  width: 100% !important;
}

.JoyCheckbox-root {
  align-items: center;
  gap: 20px;
  flex-grow: 1;
}

.addon {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.addon p {
  margin: 0;
}

.JoyCheckbox-checkbox {
  border-color: var(--Cool-gray) !important;
  border-radius: calc(var(--rad) / 4) !important;
}

.JoyCheckbox-checkbox.Joy-checked {
  color: white;
  background-color: var(--Purplish-blue) !important;
  /* transition: 0s !important; */
}

.addons .JoySheet-root {
  border-color: var(--Cool-gray) !important;
  border-radius: calc(var(--rad)/2);
}

.addons .JoySheet-root:has(.Joy-checked) {
  border-color: var(--Purplish-blue) !important;
}

.addon .price {
  white-space: nowrap;
  color: var(--Purplish-blue);
}

.JoyCheckbox-label {
  padding: 0;
}

.plan-final,
.addon-final,
.price-final {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.plan-final {
  margin-bottom: 26px;
}


.plan-final-name,
.plan-final-price {
  color: var(--Marine-blue);
  font-weight: 700;
  font-size: 1.1rem;
}

.plan-final-name a {
  font-weight: 400;
  display: block;
  text-decoration: underline;
  color: var(--Cool-gray);
}

.plan-final-name a:hover {
  color: var(--Purplish-blue);
}

.addon-final {
  margin: 12px 0;
  color: var(--Cool-gray);
  font-size: 1rem;
}

.addon-final-price {
  color: var(--Marine-blue);
}

.price-final {
  padding: 10px 0;
}

.total {
  color: var(--Cool-gray);
}

.total-price {
  font-size: 20px;
  color: var(--Purplish-blue);
  font-weight: 700;
}

button {
  padding: 16px 20px;
  color: white;
  background-color: var(--Marine-blue);
  font-size: 1rem;
  font-weight: 700;
  border-radius: 10px;
}

button.back {
  padding: 16px 0px;
  color: var(--Cool-gray);
  background-color: white;
}

.confirm {
  background-color: var(--Purplish-blue);
}

button.confirm:hover {
  background-color: #9994ff;
}

button.next:hover {
  background-color: #214c81;
}


button.back:hover {
  color: var(--Marine-blue);
}

button.next,
button.back,
button.confirm {
  position: absolute;
  bottom: 20px;
  right: 0;
}

button.back {
  left: 0;
  width: fit-content;
}

.final {
  margin-bottom: 40px;
}

.d-none {
  display: none !important;
}

@media (max-width: 991px) {

  form {
    max-width: 100%;
    min-width: calc(100% - 100px);
    width: 100%;

    height: calc(100vh - 140px);
    padding: 0 !important;
  }

  .JoyRadioGroup-root {
    flex-direction: column !important;
  }

  .plan .JoySheet-root {
    width: auto !important;
    flex-direction: row;
    height: auto;
    gap: 20px;
    padding: 20px !important;
  }

  .end {
    margin-top: 170px;
    max-height: 450px;
  }

  button.next,
  button.back,
  button.confirm {
    bottom: 60px;
  }
}