.nav-item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  color: white;
  text-transform: uppercase;
}

.nav-item .num {
  width: 42px;
  height: 42px;
  border: 2px solid var(--Light-blue);
  border-radius: 50%;
  font-size: 1.2rem;
  font-weight: 500;
  text-align: center;
}

.nav-item .text {
  margin: 0;
  font-size: 16px;
}

.nav-item .text .step {
  font-size: 14px;
  margin: 0;
  color: var(--Cool-gray);
}

.nav-item .text .name {
  margin: 0;
  font-weight: bold;
  letter-spacing: 1.5px;
}

.nav-item.active .num {
  background-color: var(--Light-blue);
  color: var(--Marine-blue);
}