.main-form {
  background-color: white;
  width: 1000px;
  border-radius: var(--rad);
  padding: var(--pad);
  display: flex;
  align-items: stretch;
  justify-content: stretch;
}

/* .content{
  display: flex;
  justify-content: center;
} */

@media (max-width:991px) {
  .main-form {
    background-color: white;
    width: 100%;
    height: calc(100vh - 43.19px);
    border-radius: 0;
    padding: 0;
    flex-direction: column;
    min-height: 750px;
  }

  .content {
    position: absolute;
    top: 85px;
    left: 50%;
    transform: translateX(-50%);
    min-width: calc(100% - 60px);
    border-radius: var(--rad);
    min-height: 650px;
  }
}